@charset "utf-8";

@import "../../assets/scss/_webfont";
@import "../../assets/scss/_element-ui";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: #333;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}





















































































































































































































































.date-range-confirm-picker {
  position: relative;

  &__header {
    border-bottom: 1px solid #eee;
    padding: 10px;
    display: flex;
  }

  &__body {
    padding: 0 10px 10px;
  }

  &__footer {
    display: flex;
    justify-content: flex-end;
    padding: 10px;
    border-top: 1px solid #eee;
  }

  &__popup {
    background-color: #fff;
    position: absolute;
    top: 40px;
    width: 750px;
    box-shadow: 2px 2px 5px rgba(#000, 0.1);

    &--left {
      left: 0;
    }

    &--center {
      left: 50%;
      transform: translateX(-50%);
    }
  }

  &__direct {
    padding: 5px;
    min-width: 85px;
    border: 1px solid #ddd;
    border-radius: 5px;
    background-color: #fff;
    text-align: center;
    cursor: pointer;
    transition: background-color 0.3s ease;

    &:hover {
      background-color: #f2f5fc;
    }

    &:not(:first-child) {
      margin-left: 5px;
    }

    &--current {
      background-color: #f2f5fc;
      font-weight: bold;
      cursor: text;
    }
  }

  &__button {
    margin-left: 10px;
    padding: 10px 10px 8px;
    min-width: 85px;
    border: 1px solid;
    background-color: #fff;
    border-radius: 5px;
    font-size: 12px;
    line-height: 1;
    cursor: pointer;
    text-align: center;
    transition: border-color 0.3s ease, background-color 0.3s ease;

    &--cancel {
      border-color: #ccc;
      color: #666;

      &:hover {
        background-color: #ebf0ff;
      }
    }

    &--apply {
      background-color: #3468eb;
      border-color: #3468eb;
      color: #fff;

      &:hover {
        background-color: #4e7ef5;
        border-color: #4e7ef5;
      }
    }
  }
}

.datePickerPopup-enter-active,
.datePickerPopup-leave-active {
  opacity: 1;
  margin-top: 0;
  transition: opacity 0.3s ease, margin-top 0.3s ease;
}
.datePickerPopup-enter,
.datePickerPopup-leave-to {
  opacity: 0;
  margin-top: -40px;
}
