@charset "utf-8";

@import "../../assets/scss/_webfont";
@import "../../assets/scss/_element-ui";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: #333;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}


























































































































































































































































.date-picker-range-change-input {
  display: flex;
  align-items: center;

  &__field {
    position: relative;
    flex: 1;
  }

  &__input--hidden {
    opacity: 0;
  }

  &__date {
    background-color: #f2f5fc;
    border-radius: 5px;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;

    &::before,
    &::after {
      position: absolute;
      top: 50%;
      left: 10px;
      transform: translateY(-50%);
    }

    &::before {
      content: attr(data-before-value);
    }

    &::after {
      content: attr(data-after-value);
    }

    &--start-switch {
      &::after {
        opacity: 0;
      }
    }

    &--end-switch {
      &::after {
        opacity: 0;
      }
    }
  }

  &__hyphen {
    padding: 0 10px;
  }
}

.fade-enter-active {
  transition: all 0.3s ease;
}
.fade-leave-active {
  transition: all 0.3s ease;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}

@keyframes start-anime {
  0% {
    opacity: 1;
    transform: translate(0, -50%);
  }
  50% {
    opacity: 0;
    transform: translate(50%, -50%);
  }
  100% {
    opacity: 0;
    transform: translate(50%, -50%);
  }
}

@keyframes end-anime {
  0% {
    opacity: 1;
    transform: translate(0, -50%);
  }
  50% {
    opacity: 0;
    transform: translate(-50%, -50%);
  }
  100% {
    opacity: 0;
    transform: translate(-50%, -50%);
  }
}
