@charset "utf-8";

@import "../assets/scss/_webfont";
@import "../assets/scss/_element-ui";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: #333;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}













































































































































































.home {
  margin-right: auto;
  margin-left: auto;
  padding: 100px 100px 500px;
  max-width: 1000px;

  h2 {
    margin-bottom: 15px;
    font-weight: bold;
    font-size: 36px;
    &:not(:first-child) {
      margin-top: 45px;
    }
  }

  p {
    margin-bottom: 20px;

    &::before {
      margin-right: 5px;
      content: "ー";
    }
  }
}

.datepicker2 {
  position: relative;
  z-index: 100;
}

.datepicker3A {
  position: relative;
  z-index: 50;
}

.datepicker3B {
  position: relative;
  z-index: 40;
}

.datepicker4A {
  position: relative;
  z-index: 35;
}

.periodDirectSelect {
  margin-top: 10px;
}
